<template>
    <div id="home">
        <img class="logo" src="../assets/img/home/logo.png" alt=""/>
        <img
                class="menu"
                @click="navFalg = true"
                src="../assets/img/home/home-top-tab.png"
        />
        <h2 class="home_title"></h2>
        <div class="home_content">
            <div class="home_content_banner" id="hpHome">
                <img class="banner" src="../assets/img/home/home-top-banner.png"/>
                <span>
                    
                 </span>
                <p class="money">
                    {{ total }}&nbsp;&nbsp;HP
                    <img src="../assets/img/home/home-arrows.png" alt=""/>
                </p>
                <p class="key" @click="toContract">: {{ hpAddress }}</p>
            </div>
            <div class="home_tab">
                <div @click="toHp()">
                    <p>
                        HP
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </p>
                    <img src="../assets/img/home/home-hp.png" alt=""/>
                </div>
                <div @click="toYsj()">
                    <p>
                        YSJ
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </p>
                    <img src="../assets/img/home/home-ysj.png" alt=""/>
                </div>
            </div>
            <div class="home_content_OrePool" id="hpKuang">
                <p class="title">HP</p>
                <div class="home_content_hp">
                    <p class="hp_sum"></p>
                    <input type="number" placeholder="0.1YSJ" v-model="ysjNum"
                    @keyup="checkNum()" @blur="checkNum"/>
                    <p class="hp_txt">{{ ysjBanner }}&nbsp;&nbsp;YSJ</p>
                    <p class="hp_txt">{{ hpNum }}HP/</p>
                    <button @click="toBuy($event)"></button>
                    <div>
                        <span></span>
                        <p>1.HP500W</p>
                        <p>2.YSJHP1YSJ10HP</p>
                        <p>3.3000HPYSJ</p>
                    </div>
                </div>
            </div>
            <div class="home_content_OrePool" id="hpProfit">
                <p class="title"></p>
                <div class="home_content_hp">
                    <div class="zhiya_txt">
                        <span></span>
                        <span>{{ info.canNum }}&nbsp;YSJ</span>
                    </div>
                    <div class="zhiya_txt">
                        <span>:</span>
                        <span>{{ info.endTime }}</span>
                    </div>
                    <div class="zhiya_txt">
                        <span>:</span>
                        <p>{{ info.qiNum }}&nbsp;YSJ <span @click="toRelease($event)"></span></p>
                    </div>
                    <div class="zhiya_txt">
                        <span></span>
                        <p>{{ info.suoNum }}&nbsp;HP<span @click="toJie($event)"></span></p>
                    </div>
                    <div class="zhiya_txt">
                        <span></span>
                        <p>{{ info.useNum }}&nbsp;HP<span @click="toTi($event)"></span></p>
                    </div>
                </div>
            </div>
            <div class="home_video" id="hpVideo">
                <p></p>
                <video src="../assets/img/video.mp4" controls="controls">
                    Your browser does not support the video tag.
                </video>
            </div>
            <div class="home_rule" id="hpTrade">
                <p class="title"></p>
                <div class="rule_box">
                    <img src="../assets/img/home/home-currency.png" alt=""/>
                    <div>
                        <p>1.5%</p>
                        <p>2. 2.5%</p>
                        <p>3. 2.5%LP</p>
                    </div>
                </div>
            </div>
        </div>

        <div v-if="navFalg">
            <div class="top_nav_box" @click="navFalg = false"></div>
            <div class="top_nav">
                <img class="logos" src="../assets/img/home/logo.png" alt=""/>
                <p>{{ short_address }}</p>
                <a href="#hpHome"  @click="navFalg = false">
                    <div>
                        <span></span>
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </div>
                </a>
                <a href="#hpKuang"  @click="navFalg = false">
                    <div>
                        <span>HP</span>
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </div>
                </a>
                <a href="#hpProfit"  @click="navFalg = false">
                    <div>
                        <span></span>
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </div>
                </a>
                <a href="#hpVideo"  @click="navFalg = false">
                    <div>
                        <span></span>
                        <img src="../assets/img/home/home-arrows.png" alt=""/>
                    </div>
                </a>
                <a href="#hpTrade"  @click="navFalg = false">
                    <div>
                    <span></span>
                    <img src="../assets/img/home/home-arrows.png" alt=""/>
                     </div>
                </a>
            </div>
        </div>
    </div>
</template>
<script>
    import $ from 'jquery'
    export default {
        data() {
            return {
                navFalg: false,
                address:'', //
                short_address:'',
                hpAddress:'TLwmSyMuT1oLWw5nR9Bm6wKs2R5Tgr3Ygf',
                ysjAddress:'TRo6yF4oTUsVgTymFZvDUW6szTmsvdFQgW',
                conAddress:'TDqkS9GYJ43wndxxP9PG8db4G9hxVb6pR7',
                ysjBanner:0,
                total:0,
                info:{
                    canNum:0,
                    endTime:'0000-00-00 00:00:00',
                    qiNum:0,
                    suoNum:0,
                    useNum:0,
                },
                ysjNum:'',
                hpNum:0,
                buyDisable:false,
                caoDisable:false,
            };
        },
        mounted() {
            let _this =this;
            var obj = setInterval(async ()=>{
                if (window.tronWeb && window.tronWeb.defaultAddress.base58) {
                    clearInterval(obj);
                    _this.address = window.tronWeb.defaultAddress.base58;
                    _this.short_address = _this.address.substr(0, 4) + '...' + _this.address.substr(_this.address.length - 4);
                    await _this.getBanner();  //
                    await _this.getInfo();

                }
            }, 10)
        },
        methods: {
            async toTi(event){
                let _this = this;
                if(this.caoDisable){
                    return false;
                }
                this.caoDisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try {
                    let instance = await window.tronWeb.contract().at(this.conAddress);
                    let res1 =  instance["Withdrawal"]();
                    res1.send({
                        feeLimit: 65000000,
                        callValue: 0,
                        shouldPollResponse: false
                    }, function (err, res) {
                        if (err == null) {
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }else{
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }
                    })
                } catch(error) {
                    console.error("trigger smart contract error",error)
                    _this.$toast('');
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async toJie(event){
                let _this = this;
                if(this.caoDisable){
                    return false;
                }
                this.caoDisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try {
                    let instance = await window.tronWeb.contract().at(this.conAddress);
                    let res1 =  instance["settleStatic"]();
                    res1.send({
                        feeLimit: 65000000,
                        callValue: 0,
                        shouldPollResponse: false
                    }, function (err, res) {
                        if (err == null) {
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }else{
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }
                    })
                } catch(error) {
                    console.error("trigger smart contract error",error)
                    _this.$toast('');
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async toRelease(event){
                let _this = this;
                if(this.caoDisable){
                    return false;
                }
                this.caoDisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try {
                    let instance = await window.tronWeb.contract().at(this.conAddress);
                    let res1 =  instance["ReleasePledge"]();
                    res1.send({
                        feeLimit: 65000000,
                        callValue: 0,
                        shouldPollResponse: false
                    }, function (err, res) {
                        if (err == null) {
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }else{
                            _this.$toast('');
                            setTimeout(function () {
                                location.reload();
                            },3000)
                        }
                    })
                } catch(error) {
                    console.error("trigger smart contract error",error)
                    _this.$toast('');
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            formatDate(time) {
                var date = new Date(time*1000);
                var YY = date.getFullYear() + '-';
                var MM = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1) + '-';
                var DD = (date.getDate() < 10 ? '0' + (date.getDate()) : date.getDate());
                var hh = (date.getHours() < 10 ? '0' + date.getHours() : date.getHours()) + ':';
                var mm = (date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()) + ':';
                var ss = (date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds());
                return YY + MM + DD +" "+hh + mm + ss;
            },
            async toBuy(event){
                let _this = this;
                if(this.ysjNum<0.1){
                    this.$toast('0.1YSJ');
                    return false;
                }
                if(this.ysjNum>this.ysjBanner){
                    this.$toast('YSJ');
                    return false;
                }
                if(this.buyDisable){
                    return false;
                }
                this.buyDisable = true;
                let toggle = event.currentTarget;
                $(toggle).css('background','#dddddd');
                try {
                    let instance = await window.tronWeb.contract().at(this.conAddress);
                    let ysjInstance = await window.tronWeb.contract().at(this.ysjAddress);
                    console.log(instance);
                    let allowanceBanner = await ysjInstance["allowance"](window.tronWeb.defaultAddress.base58,this.conAddress).call();
                    if(window.tronWeb.toDecimal(allowanceBanner)>0){
                        let res1 =  instance["investmentYSJ"](Number(_this.ysjNum)*100000000);
                        res1.send({
                            feeLimit: 100000000,
                            callValue: 0,
                            shouldPollResponse: false
                        }, function (err, res) {
                            if (err == null) {
                                _this.$toast('');
                                setTimeout(function () {
                                    location.reload();
                                },3000)
                            }
                        })
                    }else{
                        let res = await ysjInstance["approve"](this.conAddress,10000000000000);
                        res.send({
                            feeLimit: 30000000,
                            callValue: 0,
                            shouldPollResponse: false
                        }, function (err, res) {
                            if (err == null) {
                                let res1 =  instance["investmentYSJ"](Number(_this.ysjNum)*100000000);
                                res1.send({
                                    feeLimit: 100000000,
                                    callValue: 0,
                                    shouldPollResponse: false
                                }, function (err, res) {
                                    if (err == null) {
                                        _this.$toast('');
                                        setTimeout(function () {
                                            location.reload();
                                        },3000)
                                    }
                                })
                            }
                        })
                    }

                } catch(error) {
                    console.error("trigger smart contract error",error)
                    _this.$toast('');
                    setTimeout(function () {
                        location.reload();
                    },3000)
                }
            },
            async getInfo(){
                let _this = this;
                try {
                    let instance = await window.tronWeb.contract().at(this.conAddress);
                    let res = await instance["playerInfo"](window.tronWeb.defaultAddress.base58).call();
                    let timestamp =parseInt(Date.parse(new Date())/1000);
                    let startTime1 = new Date(new Date().toLocaleDateString()).getTime()/1000;
                    this.info.qiNum = 0;
                    this.info.canNum = parseFloat(window.tronWeb.toDecimal(res[1])/100000000).toFixed(2);
                    let endTime = parseInt(window.tronWeb.toDecimal(res[7]));
                    if(endTime>0){
                        this.info.endTime = this.formatDate(endTime);
                        // 
                        if(timestamp>endTime){
                            this.info.qiNum =   this.info.canNum;
                        }
                    }else{
                        this.info.endTime = '0000-00-00 00:00:00';
                    }
                    this.info.suoNum = parseFloat(window.tronWeb.toDecimal(res[5])/100000000).toFixed(2);
                    this.info.useNum =0;
                    if(endTime<=startTime1){
                        this.info.useNum = parseFloat(window.tronWeb.toDecimal(res[5])/100000000*0.2).toFixed(2);
                    }
                    console.log(this.info);
                    let res1 = await instance["WholeNetwork"]().call();
                    _this.total = parseFloat(window.tronWeb.toDecimal(res1)/100000000).toFixed(2);
                } catch(error) {
                    console.error("trigger smart contract error",error)
                }
            },
            async getBanner(){  //
                let _this = this;
                try {
                    let instance = await window.tronWeb.contract().at(this.ysjAddress);
                    console.log(instance);
                    //
                    let res = await instance["balanceOf"](window.tronWeb.defaultAddress.base58).call();

                    _this.ysjBanner  = window.tronWeb.toDecimal(res)/100000000;
                    console.log(_this.ysjBanner )

                } catch(error) {
                    console.error("trigger smart contract error",error)
                }
            },
            toContract(){
                window.open('https://tronscan.io/#/contract/'+this.hpAddress,'_self');
            },
            toHp(){
                window.open('https://sunswap.com/#/scan/detail/'+this.hpAddress,'_self');
            },
            toYsj(){
                window.open('https://sunswap.com/#/scan/detail/'+this.ysjAddress,'_self');
            },
            checkNum(){
                if(this.ysjNum>0){
                    this.hpNum = 10*this.ysjNum;
                }else{
                    this.hpNum = 0;
                }
            },
        },
        components: {},

        destroyed() {

        }
    };
</script>



